import React, { useState, useMemo } from 'react';
interface SimpleMemoExampleProps {
    onIncrease: () => void;
  }
const SimpleMemoExample: React.FC<SimpleMemoExampleProps> = ({ onIncrease }) => {
  const [number, setNumber] = useState(0);
  const [state, setState] = useState(1);
  // Memoized computation
  console.log(1
    )
  const squaredNumber = useMemo(() => {
    console.log('Computing squared number...');
    return number * number;
  }, [number]); // Dependency array: recalculates when 'number' changes

  return (
    <div>
      <p>Original Number: {number}</p>
      <p>Squared Number: {squaredNumber}</p>

    <button onClick={() => setNumber(prevNumber => prevNumber + 1)}>
        Increment Number
    </button>
    <button onClick={() => setNumber(prevNumber => prevNumber - 1)}>
        Decrement Number
    </button>

    <button onClick={onIncrease}>
         Number
    </button>
    </div>
  );
};

export default SimpleMemoExample;
